	import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"
	
	import ruiDatePicker from '@/components/rattenking-dtpicker.vue';
	export default {
		components: {
			uniNavBar,
			ruiDatePicker,
			
		},
		data() {
			return {
				tabIndex: 0,
				specClass: 'none',
				showImg:true,
				bigImg:'',
                showBigImg:false,
				orderList:[111],//订单列表
				freightList:[],//运费模版ID
				classifyName:'',//类名
				classifyId:'',//类名
				goodsName:'',//商品名称
				goodsPrice:null,//商品价格
				stockNum:null,//库存数
				fenxiao:null,//商品分销金额
				deliveryName:'',//运费模板id
				deliveryId:'',//运费模板id
				openTime:'',//开售时间
				content:'',//描述
				goodsStatus:10,//商品状态 10:上架 20:下架 30:售空
				token:'',
				goodsStatusIndex:0,
				uploadPictures:'',//
				currentTime:null,//当期时间
				selectCurrentTime:null,//选择的预售时间
				goodsWeight:'',//商品重量
				newTime :'',
				showAdd:false,//增加规格
				specIndex:0,
				addSpecIndex:0,
				tabAddIndex:0,//已有规格名添加规格值
				jumpImdex:0,
				specAttr:[
					{
						index:0,
						group_name:'',
						group_id:'',
						spec_tab:false,
						spec_items:[
							{
								index:0,
								id:0,
								spec_value_id:'',
								spec_value:'',
							}
							
						]
					}
					
				],//规则列表
				showSpecList:false,
				specList:[
					/* {
						index:0,
						spec_sku_id:'',
						spec_name:'',
						form:[
							{
								goods_price:null,
								stock_num:null,
								goods_weight:null
							}
						]
					} */
				],
				specName:'',//规格名
				specValue:'',//规格值
				tabList:[
					{
						index:0,
						name:'上架',
						goods_status:10
					},
					{
						index:1,
						name:'下架',
						goods_status:20
					},
					{
						index:2,
						name:'售空',
						goods_status:30
					}
				]
				
				
				
			};
		},
		
		async onLoad(){
			let userInfo=uni.getStorageSync('userInfo');
			if(userInfo!==undefined){
				this.token = userInfo.token;
				
			}else{
				uni.navigateTo({
					url:'/pages/login/login'
				})
			}
			
			
			
			//获取当前时间
			//补位 当某个字段不是两位数时补0
			function fnW(str){
				var num;
				str>10?num=str:num="0"+str;
				return num;
			} 
			function fnDate(){
				
				// new Date(now .getTime() + 300000); 
				//1. js获取当前时间
				var date=new Date();
				//2. 获取当前分钟
				var min=date.getMinutes();
				//3. 设置当前时间+5分钟：把当前分钟数+5后的值重新设置为date对象的分钟数
				date.setMinutes(min+5);
				//4. 测试
				console.log(date);
				
				var year=date.getFullYear();//当前年份
				var month=date.getMonth();//当前月份
				var data=date.getDate();//天
				var hours=date.getHours();//小时
				var minute=date.getMinutes();//分
				var second=date.getSeconds();//秒
				var time=year+fnW((month+1))+fnW(data)+fnW(hours)+fnW(minute)+fnW(second);
				return time;
			}
			this.currentTime = fnDate();
		},
		
		
		methods: {
			//确认添加
			addConfirm(){
				let self = this;
				//验证
				if(!self.goodsName){ 
					uni.showToast({title: '请输入商品名称',icon:"none"});
					return ; 
				}
				if(!self.classifyId){ 
					uni.showToast({title: '请选择商品类别',icon:"none"});
					return ; 
				}
				if(!self.uploadPictures){
					uni.showToast({title: '请选择商品图片',icon:"none"});
					return ; 
				}
				/* if(!self.goodsPrice  ){ 
					uni.showToast({title: '请输入商品价格',icon:"none"});
					return ; 
				} */
				/* if(!self.stockNum){ 
					uni.showToast({title: '请输入商品库存数',icon:"none"});
					return ; 
				} */
				/* if(!self.goodsWeight){ 
					uni.showToast({title: '请输入商品重量',icon:"none"});
					return ; 
				} */
				
				if(!self.content){ 
					uni.showToast({title: '请输入详情描述',icon:"none"});
					return ; 
				}
				if(!self.openTime){ 
					uni.showToast({title: '请选择开售时间',icon:"none"});
					return ; 
				}else{
					
					if(self.currentTime == self.newTime){
						uni.showToast({title: '预售时间要大于当前时间',icon:"none"});
						return ; 
					}
					if(self.currentTime > self.selectCurrentTime){
						uni.showToast({title: '预售时间要大于当前时间',icon:"none"});
						return ; 
					}
				}
				
				if(!self.fenxiao){ 
					uni.showToast({title: '请输入分销金额',icon:"none"});
					return ; 
				}
				if(!self.deliveryId){ 
					uni.showToast({title: '请选择运费模板',icon:"none"});
					return ; 
				}
			
				uni.request({
					url: 'https://wxshop.qclike.cn/api/admins/addGoodsSome',
					method:'POST',
					data:{
						token:self.token,
						row:{
							goods_name:self.goodsName,
							category_id:self.classifyId,
							content:self.content,
							sales_starttime:self.openTime,
							fenxiao:self.fenxiao,
							images:self.uploadPictures,
							delivery_id:self.deliveryId,
							goods_status:self.goodsStatus
							
						},
						spec_many:{
							spec_attr:self.specAttr,
							spec_list:self.specList,
							
						}
						
					},
					success: function(res) {
						if(res.data.code == 1){
							uni.navigateTo({
								url:'/pages/tabBar/home'
							})
						} else if(res.data.code == 0){
							let msg = res.data.msg
							uni.showToast({title: msg,icon:"none"});
						}
					}
				});
			}, 
			//禁止手指滑动
			stopTouchMove: function() {
			  return false;
			},
			//改变时间
			bindChange(picker){
				var a = picker.split(' ')[0];
				var b = picker.split(' ')[1];
				var aArr = a.split('-');
				var bArr = b.split(':');
				
				this.selectCurrentTime = aArr[0]+aArr[1]+aArr[2]+bArr[0]+bArr[1]+bArr[2];
				//进行强制渲染
				 this.$forceUpdate();
			    this.openTime = picker;
				
			},
			//点击取消操作
			bindCancel(){
				console.log(2222)
			},
			//点击返回按钮
			 backButton(){ 
				uni.navigateTo({
					url:'/pages/tabBar/home'
				})
			},
			//选择商品状态
			selectItem(todo){
				let self = this;
				self.goodsStatusIndex = todo.index;
				self.goodsStatus = todo.goods_status;
				
			},
			//上传图片
			changeImg(){
				let self = this;
				uni.chooseImage({
					count: 1,
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album','camera '], //从相册选择或相机
					success: function (res) {
					 const tempFilePaths = res.tempFilePaths;
					 const uploadTask = uni.uploadFile({
					  url : 'https://wxshop.qclike.cn/api/admins/upload',
					  filePath: tempFilePaths[0],
					  name: 'file',
					  data:{
						  token:self.token,
						  file:tempFilePaths[0]
					  },
					  method:'POST',
					  success: function (uploadFileRes) {
						  if(JSON.parse(uploadFileRes.data).code == 1){
							  self.showImg = !self.showImg;
							   self.uploadPictures = JSON.parse(uploadFileRes.data).data.url;
						  }
						  console.log(9999,JSON.parse(uploadFileRes.data).data.url)
						 /* //进行强制渲染
						  self.$forceUpdate(); */
						   // console.log('进行强制渲染',uploadFileRes.data);
						}
					 });
				 
					 uploadTask.onProgressUpdate(function (res) {
						  self.percent = res.progress;
						  /* console.log('上传进度' + res.progress);
						  console.log('已经上传的数据长度' + res.totalBytesSent);
						  console.log('预期需要上传的数据总长度' + res.totalBytesExpectedToSend); */
						 });
					},
					error : function(e){
					 console.log(e);
					}
				});
			},
			
			//查看大图
			lookBigImg(todo){
				let self = this;
				self.bigImg = todo;
				self.showBigImg = true;
			},
			 //关闭查看大图
			cancelDownload:function () {
				let self = this;
				self.showBigImg = false;
			},
			//弹窗开关
			toggleSpec() {
				if(this.specClass === 'show'){
					this.specClass = 'hide';
					setTimeout(() => {
						this.specClass = 'none';
					}, 250);
				}else if(this.specClass === 'none'){
					this.specClass = 'show';
				}
			},
			//确认按钮
			toggleSpecTab(todo){
				if(this.specClass === 'show'){
					this.specClass = 'hide';
					setTimeout(() => {
						this.specClass = 'none';
					}, 250);
				}else if(this.specClass === 'none'){
					this.specClass = 'show';
				}
				if(todo && this.tabIndex == 1){
					
					this.classifyId = todo.id;
					this.classifyName = todo.name;
				}
				//运费模版ID
				if(todo && this.tabIndex == 2){
					this.deliveryId = todo.id;
					this.deliveryName = todo.name;
				}
				
			},
			
			//删除图片
			deleteTemplate(){
				let self = this;
				self.showImg = !self.showImg;
				self.uploadPictures = '';
			},
			//单规则操作
			singleRule(){
				 uni.navigateTo({
					url:'/pages/goods/addgoods'
				})
				 
			},
			//多规则操作
			multipleRule(){
				console.log('多规则操作')
			},
			//运费模板
			freightTemplate(index){
				let self = this;
				self.tabIndex = index;
				if(self.specClass === 'show'){
					self.specClass = 'hide';
					setTimeout(() => {
						self.specClass = 'none';
					}, 250);
				}else if(self.specClass === 'none'){
					self.specClass = 'show';
				}
				//选择类别接口
